<template>
<main>
	<div id="Background" v-if="registrationBackgroundShow"></div>
	<!---->
	<header id="registrationBack">
			<router-link to="/login"><div></div></router-link>
			<span>&nbsp;&nbsp;1/3</span>
	</header>
	
	<!---->
	<section id="registrationPart">
			<p></p>
			<div id="registrationPlace">&nbsp;86<div></div></div>
			<input type="text" placeholder="" id="registrationNumber">
			<!--<img src="../../assets/53.jpg">-->
			<button @click="registrationVerify"></button>
			
			<!---->
			<span id="registrationAgreement">
				<span class="registrationService"></span>
				
				<span class="registrationService"></span>
			</span>
	</section>
	
	
	
	<!---->
	<section id="registrationAgree" v-if="registrationAlertShow">
		<h3></h3>
		<div id="registrationAgreeFont">
			<p style="width: 93%;left: 3.5%;line-height: 1.7;position: relative;padding-top: 20px;">
				
				<b></b>
			</p>
			<p style="width: 93%;left: 3.5%;line-height: 1.7;position: relative;">
				<b></b>
				privacy@ctrip.com
			</p>
			<p style="width: 93%;left: 3.5%;line-height: 1.7;position: relative;padding-bottom: 25px;">
				<b><span style="text-decoration: underline;color: #2577e3;font-size: 16px;"></span><span style="text-decoration: underline;color: #2577e3;font-size: 16px;"></span></b>
			</p>
		</div>
		
		<section id="registrationAgreeOrNo">
			<ul>
				<router-link to="/login"><li></li></router-link>
				<li @click="registrationAgree"></li>
			</ul>
		</section>
	</section>
	
	
	
</main>
</template>

<script>
export default{
	data(){
		return{
			registrationAlertShow:true,
			registrationBackgroundShow:true
		};
	},
		methods:{
			registrationAgree(){
				this.registrationAlertShow=!this.registrationAlertShow
				this.registrationBackgroundShow=!this.registrationBackgroundShow
			},
			registrationVerify(){
				alert("~")
			},
		}
}
</script>

<style scoped>
*{
	padding: 0px;
	margin: 0px;
	font-size: 14px;
}
#Background{
	position: absolute;
	z-index: 1;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,0.5);
}
#registrationBackground{
	z-index: 998;
	position: fixed;
	width: 100%;
	height: 100%;
}
#registrationBack{
	width: 100%;
	height: 44px;
	/*background: pink;*/
}
#registrationBack div{
	position: absolute;
	top: 17px;
	left: 4%;
	color: black;
	width: 10px;
	height: 10px;
	border-bottom: 2px solid dodgerblue;
	border-left: 2px solid dodgerblue;
	transform: rotate(45deg);
}
#registrationBack span{
	display: block;
	font-size: 17px;
	color: #999;
	line-height: 44px;
	text-align: center;
}
#registrationPart p{
	position: relative;
	top: 50px;
	display: block;
	width: 100%;
	height: 25px;
	font-size: 25px;
	color: #333333;
	line-height: 25px;
	text-align: center;
}
#registrationPlace{
	position: relative;
	top: 100px;
	width: 86%;
	height: 54px;
	margin: 0 auto;
	line-height: 54px;
	font-size: 16px;
	border-bottom: 1px solid lightgray;
}
#registrationPlace div{
	position: absolute;
	top: 22px;
	right: 4%;
	color: black;
	width: 10px;
	height: 10px;
	border-top: 2px solid lightgray;
	border-right: 2px solid lightgray;
	transform: rotate(45deg);
}
#registrationNumber{
	position: relative;
	left: 7%;
	top: 100px;
	outline-style: none;
	border: 0px;
	border-bottom: 1px solid lightgray;
	width: 86%;
	height: 54px;
	font-size: 17px;
	line-height: 24px;
	background: none;
}
#registrationPart button{
	position: relative;
	width: 86%;
	top: 130px;
	border: none;
	border-radius: 5px;
	left: 7%;
	height: 44px;
	color: white;
	font-size: 17px;
	text-align: center;
	background: #ff9a14;
}
#registrationAgreement{
	font-size: 13px;
	/*z-index: 1;*/
	color: darkgray;
	display: block;
	width: 100%;
	position: relative;
	top: 170px;
	bottom: 0px;
	text-align: center;
}
.registrationService{
	font-size: 13px;
	color: #099fde;
}
#registrationPart img{
	position: absolute;
	top: 242px;
	right: 16%;
}
#registrationAgree{
	z-index: 1;
	border-top: 1px solid whitesmoke;
	position: absolute;
	/*top: 0;*/
	bottom: 0px;
	width: 100%;
	height: 472px;
	background: white;
}
#registrationAgreeFont{
	border-top: 1px solid whitesmoke;
	max-height: 380px;
	overflow-y: scroll;
	/*overflow: hidden;*/
}
#registrationAgreeFont p{
	font-size: 15px;
	padding-bottom: 10px;
}
#registrationAgree h3{
	position: relative;
	text-align: center;
    font-size: 17px;
    font-weight: bold;
    line-height: 48px;
}
#registrationAgreeOrNo{
	position: fixed;
	bottom: 0;
	width: 100%;
}
#registrationAgreeOrNo ul li{
	display: block;
	width: 50%;
	height: 44px;
	line-height: 44px;
	font-size: 16px;
	background: white;
	float: left;
	text-align: center;
}
#registrationAgreeOrNo ul li:nth-child(1){
	color: #666;
	border-top: 0.5px solid lightgray;
}
#registrationAgreeOrNo ul li:nth-child(2){
	color: white;
	background: #FF9913;
}
</style>


